body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

p{
  padding:0;
  margin:0;
}

.oft{
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow:hidden;
}

#head{
  padding:15px;
  font-size:21px;
  display:flex;
  border-bottom:1px solid #bdbdbd;
  padding-bottom:25px;
}

#head-logo{
  margin-right:10px;
  width:180px;
  line-height:33px;
}

#head-search{
  width:80%;
}

#search-input{
  width:calc(100% - 70px);
  box-sizing: border-box;
  padding:7px 10px;
  font-size:14px;
  outline:none;
  border:1px solid #dedede;
}

#go{
  width:50px;
  margin-left:10px;
  height:33px;
  padding-top:4px;
  border:1px solid #dedede;
  cursor:pointer;
  outline:none;
}

#search-sort{
  margin-top:10px;
}

.search-radio{
  display:inline-block;
  margin-top:10px;
  margin-right:10px;
}

.point{
  width:16px;
  height:16px;
  background:#ffe172;
  display:inline-block;
  margin-right:5px;
}

#load_more{
  width:100%;
  padding:20px 0;
  text-align:center;
  cursor:pointer;
}

.item{
  width:100%;
  padding:15px;
  box-sizing: border-box;
  display:flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
}

.item-owner{
  width:200px;
  font-size: 14px;
  display:flex;
  flex-direction:column;
}

.item-description{
  width:150px;
  max-width:150px;
  word-break:break-word;
  font-size:12px;
  margin-left:10px;
}

.item-short-icon{
  font-size:21px;
  margin-bottom:5px;
  cursor:default;
}

.item-short-amount{
  cursor:default;
}

.item-short-info{
  min-width:35px;
  margin-left:15px;
  margin-right:15px;
  display:flex;
  align-items: center;
  justify-content: center;
  flex-direction:column;
}

.item-hr{
  width:98%;
  margin-left:1%;
  box-sizing: border-box;
  height:2px;
  background:#f9f9f9;
}

.item-license{
  font-size:12px;
  width:150px;
}

.item-link{
  color:#2196f3;
  font-size:36px;
  cursor:pointer;
}

